import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SpotifyService } from '../../services/spotify.service';
import { Album } from '../artist/album.model';
declare var $:any;

@Component({
  selector: 'app-album',
  templateUrl: './album.component.html',
  styleUrls: ['./album.component.css']
})
export class AlbumComponent implements OnInit {
  id: string;
  album: Album[];

  constructor(
    private spotifyService: SpotifyService,
    private activatedRoute: ActivatedRoute) {

    }

  ngOnInit() {
    this.activatedRoute.params.map(params => params['id'])
      .subscribe((id) => {
        this.spotifyService.getAlbum(id)
          .subscribe(album => {
            this.album = album
          })
      })
  }

  playAudio(track) {
    let music = $(`#music-${track.id}`)[0];
    let pButton = $(`#pButton-${track.id}`)[0];
  	if (music.paused) {
  		music.play();
  		$(pButton).removeClass('play');
  		$(pButton).addClass('pause');
  	} else {
  		music.pause();
  		$(pButton).removeClass('pause');
  		$(pButton).addClass('play');
  	}
    music.addEventListener("ended", function() {
      if (music.ended) {
        $(pButton).removeClass('pause');
      	$(pButton).addClass('play');
      }
    });
  }
}



